.container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 375px;
  padding: 0 20px 0 20px;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1440px) {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}
