@import '../../sass/vars';

.statisticsWrapper {
  width: 400px;
  background-color: $secondary-bg-color;
  border-radius: 2px;
  overflow: hidden;
}

.title {
  margin: 30px 0;
  text-align: center;
}

.statList {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 7px 0 7px;
  color: $white;
}
